'use client';

import { useState } from 'react';
import Link from 'next/link';
import { ChevronLeft, ChevronRight, Calendar as CalendarIcon, ArrowRight } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Booking } from '@/types';

interface CalendarSectionProps {
    bookings: Booking[];
}

export default function CalendarSection({ bookings }: CalendarSectionProps) {
    const [currentDate, setCurrentDate] = useState(new Date());
    const [selectedDate, setSelectedDate] = useState<string | null>(null);

    // --- LOGIKA KALENDER ---
    const getDaysInMonth = (date: Date) => {
        const year = date.getFullYear();
        const month = date.getMonth();
        const daysInMonth = new Date(year, month + 1, 0).getDate();
        const firstDay = new Date(year, month, 1).getDay(); 
        return { daysInMonth, firstDay };
    };

    const { daysInMonth, firstDay } = getDaysInMonth(currentDate);
    
    const prevMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() - 1, 1));
    const nextMonth = () => setCurrentDate(new Date(currentDate.getFullYear(), currentDate.getMonth() + 1, 1));

    // Helper untuk format YYYY-MM-DD
    const formatDateKey = (date: Date) => {
        return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
    };

    // Tanggal Hari Ini (untuk highlight)
    const today = new Date();
    const todayStr = formatDateKey(today);

    // Normalize booking dates to local YYYY-MM-DD to avoid timezone/format mismatch
    const normalizeBookingDate = (tanggal_booking?: string | null) => {
        if (!tanggal_booking) return null;
        const d = new Date(tanggal_booking);
        if (!isNaN(d.getTime())) {
            return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')}`;
        }
        // fallback: try splitting common date-time formats
        return String(tanggal_booking).split('T')[0].split(' ')[0];
    };

    // Cek Status Tanggal
    const checkDateStatus = (day: number) => {
        const dateStr = `${currentDate.getFullYear()}-${String(currentDate.getMonth() + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
        
        // Cek Booking (normalize booking.tanggal_booking before comparing)
        const count = bookings.filter(b => {
            const bookingDate = normalizeBookingDate(b.tanggal_booking);
            return bookingDate === dateStr && b.status_booking?.nama_status !== 'Dibatalkan';
        }).length;

        return {
            dateString: dateStr,
            status: count > 0 ? 'booked' : 'available',
            count
        };
    };

    const handleDateClick = (dateString: string) => {
        setSelectedDate(dateString);
    };

    return (
        <section id="booking" className="py-20 bg-white">
            <div className="max-w-5xl mx-auto px-6 text-center">
                <span className="text-[#D93F21] font-bold uppercase tracking-widest text-xs bg-[#D93F21]/10 px-4 py-2 rounded-full">Availability</span>
                <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4 mt-4">Pilih Waktu Mainmu !</h2>
                <p className="text-gray-500 mb-12 max-w-xl mx-auto text-lg">
                    Cek ketersediaan lapangan secara real-time. Tanggal dengan tanda <span className="text-[#D93F21] font-bold">merah</span> berarti sudah ada jadwal terisi.
                </p>

                <div className="bg-white rounded-[2.5rem] shadow-[0_20px_50px_rgba(0,0,0,0.05)] p-8 md:p-12 border border-gray-100">
                    {/* Header Kalender */}
                    <div className="flex justify-between items-center mb-10">
                        <button onClick={prevMonth} className="p-4 hover:bg-gray-50 rounded-full text-gray-600 transition hover:text-[#D93F21] border border-gray-200 hover:border-[#D93F21]">
                            <ChevronLeft className="w-6 h-6" />
                        </button>
                        <h3 className="text-3xl font-bold text-gray-900 tracking-tight">
                            {currentDate.toLocaleDateString('en-US', { month: 'long', year: 'numeric' })}
                        </h3>
                        <button onClick={nextMonth} className="p-4 hover:bg-gray-50 rounded-full text-gray-600 transition hover:text-[#D93F21] border border-gray-200 hover:border-[#D93F21]">
                            <ChevronRight className="w-6 h-6" />
                        </button>
                    </div>

                    {/* Nama Hari */}
                    <div className="grid grid-cols-7 gap-2 mb-6">
                        {['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'].map(day => (
                            <div key={day} className="text-gray-400 font-bold uppercase text-xs tracking-widest">{day}</div>
                        ))}
                    </div>
                    
                    {/* Grid Tanggal */}
                    <div className="grid grid-cols-7 gap-2 md:gap-4">
                        {Array.from({ length: firstDay }).map((_, i) => (
                            <div key={`empty-${i}`} className="h-14 md:h-20"></div>
                        ))}

                        {Array.from({ length: daysInMonth }).map((_, i) => {
                            const day = i + 1;
                            const { status, dateString } = checkDateStatus(day);
                            const isSelected = selectedDate === dateString;
                            const isToday = dateString === todayStr; 
                            const isPast = dateString < todayStr; 

                            return (
                                <button
                                    key={day}
                                    onClick={() => !isPast && handleDateClick(dateString)}
                                    disabled={isPast}
                                    className={`
                                        relative h-14 md:h-20 rounded-2xl border-2 flex flex-col items-center justify-center transition-all duration-200
                                        ${isPast
                                            ? 'opacity-80 bg-gray-50 border-gray-100 text-gray-300 cursor-not-allowed' 
                                            : isSelected 
                                                ? 'bg-[#D93F21] border-[#D93F21] text-white shadow-lg scale-105 z-10' 
                                                : status === 'booked'
                                                    ? 'bg-red-50 border-red-100 text-red-500 hover:border-red-300'
                                                    : isToday
                                                        ? 'bg-gray-100 border-gray-400 text-gray-900 font-bold hover:border-gray-500' 
                                                        : 'bg-white border-gray-100 text-gray-700 hover:border-[#D93F21] hover:shadow-lg'
                                        }
                                    `}
                                >
                                    <span className={`text-xl ${isSelected || isToday ? 'font-bold' : 'font-medium'}`}>{day}</span>
                                    
                                    {/* Label "Today" Kecil */}
                                    {isToday && !isSelected && (
                                        <span className="absolute top-1 left-1/2 -translate-x-1/2 text-[9px] font-bold text-gray-500 uppercase tracking-wide">Today</span>
                                    )}

                                    {/* Dot Indicator untuk Booked (Hanya muncul jika bukan tanggal lewat) */}
                                    {status === 'booked' && !isSelected && !isPast && (
                                        <span className="absolute bottom-3 w-2 h-2 bg-red-500 rounded-full"></span>
                                    )}
                                </button>
                            );
                        })}
                    </div>

                    {/* Panel Info */}
                    {selectedDate && (
                        <div className="mt-10 bg-[#111] rounded-[2rem] p-8 border border-gray-800 flex flex-col md:flex-row items-center justify-between gap-6 animate-in fade-in slide-in-from-top-4">
                            <div className="text-left">
                                <p className="text-gray-400 text-sm mb-2 font-medium uppercase tracking-wide">Tanggal Dipilih</p>
                                <h4 className="text-2xl font-bold text-white flex items-center gap-3">
                                    <CalendarIcon className="w-6 h-6 text-[#D93F21]" />
                                    {new Date(selectedDate).toLocaleDateString('id-ID', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' })}
                                </h4>
                                <p className={`text-sm mt-2 font-medium ${checkDateStatus(new Date(selectedDate).getDate()).status === 'booked' ? 'text-red-400' : 'text-green-400'}`}>
                                    {checkDateStatus(new Date(selectedDate).getDate()).status === 'booked' 
                                        ? '⚠️ Ada jadwal terisi. Cek jam yang tersedia.' 
                                        : '✅ Tanggal ini kosong. Bebas pilih jam!'}
                                </p>
                            </div>
                            <Link href={`/pelanggan/booking/create?date=${selectedDate}`}>
                                <Button className="bg-[#D93F21] hover:bg-[#b9351b] text-white px-8 py-7 text-lg rounded-2xl shadow-lg font-bold transition-transform hover:scale-105">
                                    Lanjut Booking <ArrowRight className="ml-3 w-6 h-6" />
                                </Button>
                            </Link>
                        </div>
                    )}
                </div>
            </div>
        </section>
    );
}