'use client';

import { useEffect, useState } from 'react';
import axios from '@/lib/axios';
import Link from 'next/link';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';

import HeroSection from '@/components/pelanggan/homepage/HeroSection';
import AboutSection from '@/components/pelanggan/homepage/AboutSection';
import CalendarSection from '@/components/pelanggan/homepage/CalendarSection';
import ProductSection from '@/components/pelanggan/homepage/ProductSection';
import Navbar from '@/components/pelanggan/Navbar';

import { ProdukResto } from '@/types';

export default function Home() {
  useAuth({ middleware: 'guest', redirectIfAuthenticated: '/pelanggan/home' });

  const [products, setProducts] = useState<ProdukResto[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);


  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await axios.get('/api/produkRestos');
        setProducts(res.data);
      } catch (err: any) {
        console.error(err);
        setError('Gagal memuat produk.');
        toast?.error?.('Gagal memuat produk.');
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  return (
    <div className="min-h-screen bg-[#f8f9fa] font-sans text-gray-900 pb-10">
      <Navbar />

      <div className="bg-yellow-50 border border-yellow-200 text-yellow-800 p-3 text-center">
        Anda belum login. <Link href="/login" className="font-semibold text-[#D93F21] underline">Masuk</Link> atau <Link href="/register" className="font-semibold text-[#D93F21] underline">Daftar</Link> untuk membuat booking.
      </div>

      <HeroSection />
      <AboutSection />
      <CalendarSection bookings={[]} />
      <ProductSection products={products} loading={loading} />

      {error && (
        <div className="max-w-3xl mx-auto mt-6 px-6">
          <div className="p-4 bg-red-50 border border-red-200 rounded-lg text-red-700 text-center">{error}</div>
        </div>
      )}
    </div>
  );
}