'use client';

import Link from 'next/link';
import { Search, LogOut, Bell } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Popover, PopoverTrigger, PopoverContent } from '@/components/ui/popover';
import { useAuth } from '@/hooks/useAuth'; 

export default function Navbar() {
    // Use useAuth without middleware so navbar can render for guests without redirecting
    const { user, logout } = useAuth();

    return (
        <nav className="sticky top-4 z-50 mx-4 md:mx-12 mt-4 bg-[#1a1a1a] text-white py-3 px-6 md:px-8 rounded-2xl shadow-xl border border-gray-800/50 backdrop-blur-md transition-all duration-300">
            <div className="flex justify-between items-center">
                <div className="text-xl md:text-2xl font-bold tracking-tight">
                    MainDi<span className="text-[#D93F21]">Hatta</span>.id
                </div>
                
                <div className="hidden md:flex items-center gap-8 text-sm font-medium text-gray-300">
                    <Link href={user ? "/pelanggan/home" : "/"} className="text-white hover:text-[#D93F21] transition">Beranda</Link>
                    <Link href={user ? "/pelanggan/dashboard#about" : "#about"} className="hover:text-[#D93F21] transition">Tentang</Link>
                    <Link href={user ? "/pelanggan/produk" : "#produk"} className="hover:text-[#D93F21] transition">Kuliner</Link>
                    <Link href={user ? "/pelanggan/booking/create" : "/login"} className="hover:text-[#D93F21] transition">Booking</Link>
                    {user && (
                        <Link href="/pelanggan/booking/riwayat" className="hover:text-[#D93F21] transition">Riwayat Booking</Link>
                    )}
                </div>

                <div className="flex items-center gap-4">
                    <div className="relative hidden sm:block">
                        <input 
                            type="text" 
                            placeholder="Search..." 
                            className="bg-white/10 text-white px-4 py-1.5 rounded-full text-sm focus:outline-none border border-transparent focus:border-[#D93F21] w-32 focus:w-56 transition-all placeholder-gray-400"
                        />
                        <Search className="w-4 h-4 absolute right-3 top-2 text-gray-400" />
                    </div>
                    <div className="flex items-center gap-3 pl-4 border-l border-gray-700">
                        {user ? (
                            <>
                                <NotificationsBell />

                                <Link href="/profile">
                                    <span className="text-sm text-gray-300 hidden md:block">Hi, {user?.name}</span>
                                </Link>
                                <Button variant="ghost" size="icon" onClick={logout} className="text-white hover:bg-white/10 hover:text-red-400 rounded-full">
                                    <LogOut className="w-5 h-5" />
                                </Button>
                            </>
                        ) : (
                            <div className="flex items-center gap-3">
                                <Link href="/login" className="text-sm text-white underline">Masuk</Link>
                                <Link href="/register" className="text-sm text-white ml-2">Daftar</Link>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </nav>
    );
}

function NotificationsBell() {
    const { user } = useAuth();
    const { items, markAllRead, markRead, remove } = useNotifications(!!user);
    const unread = items.filter(i => !i.read).length;

    return (
        <Popover onOpenChange={(open) => { if (open) markAllRead(); }}>
            <PopoverTrigger asChild>
                <Button variant="ghost" size="icon" className="text-white hover:bg-white/10 relative">
                    <Bell className="w-5 h-5" />
                    {unread > 0 && (
                        <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] rounded-full px-1.5">{unread}</span>
                    )}
                </Button>
            </PopoverTrigger>
            <PopoverContent className="w-80">
                <div className="flex items-center justify-between mb-2">
                    <div className="font-medium">Notifikasi</div>
                    <button className="text-sm text-gray-500 hover:text-gray-700" onClick={(e) => { e.stopPropagation(); markAllRead(); }}>Tandai semua dibaca</button>
                </div>

                <div className="max-h-60 overflow-y-auto space-y-2">
                    {items.length === 0 ? (
                        <div className="text-sm text-gray-500">Tidak ada notifikasi</div>
                    ) : (
                        items.map(i => (
                            <div key={i.id} className={`p-2 rounded-md border ${i.read ? 'bg-white' : 'bg-gray-50'}`}>
                                <div className="flex items-start justify-between">
                                    <div className="text-sm text-gray-800">
                                        {i.type === 'booking:pending' && 'Booking baru perlu konfirmasi'}
                                        {i.type === 'booking:confirmed' && `Booking dikonfirmasi ${i.data?.kode_booking ?? ''}`}
                                        {i.type === 'booking:rejected' && `Booking dibatalkan ${i.data?.kode_booking ?? ''}`}
                                        {i.type === 'booking:status_changed' && `Status booking diperbarui ${i.data?.kode_booking ?? ''}`}
                                    </div>
                                    <div className="flex flex-col items-end gap-2">
                                        <button onClick={() => markRead(i.id)} className="text-xs text-gray-500">Tandai</button>
                                        <button onClick={() => remove(i.id)} className="text-xs text-red-500">Hapus</button>
                                    </div>
                                </div>
                                <div className="text-[11px] text-gray-500 mt-1">{i.data?.kode_booking ? `#${i.data.kode_booking}` : ''}</div>
                            </div>
                        ))
                    )}
                </div>
            </PopoverContent>
        </Popover>
    );
}