'use client';

import Image from 'next/image';
import Link from 'next/link';
import { MapPin, Calendar as CalendarIcon, Clock, User, ArrowRight } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function HeroSection() {
    return (
        <section className="relative h-[600px] md:h-[700px] flex items-center bg-black overflow-hidden mx-4 md:mx-12 mt-6 rounded-[2.5rem] shadow-2xl group">
            {/* Background Image */}
            <div className="absolute inset-0 z-0">
                <Image 
                    src="/images/jumbotron-bg.png" 
                    alt="Hero Basketball"
                    fill
                    className="object-cover opacity-60 transition-transform duration-[20s] group-hover:scale-110" 
                    priority
                />
                <div className="absolute inset-0 bg-gradient-to-r from-black/90 via-black/30 to-transparent"></div>
            </div>

            <div className="relative z-10 px-8 md:px-16 w-full flex flex-col lg:flex-row items-center justify-between gap-10 h-full">
                
                {/* Teks Hero */}
                <div className="max-w-2xl space-y-8 pt-20 lg:pt-0">
                    <h1 className="text-5xl md:text-7xl font-extrabold text-white leading-[1.1] tracking-tight">
                        Pesan Lapangan <br/>
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#D93F21] to-orange-500">Basket</span> Tanpa <br/>
                        Hambatan.
                    </h1>
                    <p className="text-gray-300 text-lg max-w-lg leading-relaxed font-light">
                        Dapatkan kemudahan dan kepastian dalam mereservasi lapangan basket berkualitas tinggi secara online.
                    </p>
                </div>

                {/* Mini Form Card */}
                <div className="hidden lg:block w-[400px] bg-white/10 backdrop-blur-md p-8 rounded-[2rem] border border-white/20 shadow-2xl transform transition-transform duration-500 hover:-translate-y-2">
                    <h3 className="font-bold text-white text-xl mb-6 flex items-center gap-2 border-b border-white/10 pb-4">
                        Booking Lapangan Basket Anda!
                    </h3>
                    
                    <div className="space-y-5">
                        <div className="space-y-2">
                            <label className="text-xs text-gray-300 uppercase font-bold tracking-wider ml-1">Lokasi</label>
                            <div className="bg-black/40 border border-white/5 p-4 rounded-2xl flex items-center gap-4 text-white group hover:border-[#D93F21]/50 transition-colors">
                                <div className="p-2 bg-[#D93F21]/20 rounded-lg group-hover:bg-[#D93F21] transition-colors">
                                    <MapPin className="w-5 h-5 text-[#D93F21] group-hover:text-white" />
                                </div>
                                <span className="font-medium">Hatta Sport Center</span>
                            </div>
                        </div>

                        <div className="space-y-2">
                            <label className="text-xs text-gray-300 uppercase font-bold tracking-wider ml-1">Tanggal Main</label>
                            <div className="bg-black/40 border border-white/5 p-4 rounded-2xl flex items-center gap-4 text-white group hover:border-[#D93F21]/50 transition-colors cursor-pointer">
                                <div className="p-2 bg-[#D93F21]/20 rounded-lg group-hover:bg-[#D93F21] transition-colors">
                                    <CalendarIcon className="w-5 h-5 text-[#D93F21] group-hover:text-white" />
                                </div>
                                <span className="font-medium text-gray-400">Pilih di Kalender Bawah</span>
                            </div>
                        </div>

                        <Link href="/pelanggan/booking/create" className="block pt-2">
                            <Button className="w-full bg-[#D93F21] hover:bg-[#b9351b] h-14 rounded-2xl font-bold text-white text-lg shadow-lg">
                                Cari Jadwal
                            </Button>
                        </Link>
                    </div>
                </div>
            </div>
        </section>
    );
}